'use strict';

// MODULES //

import isnan from '@stdlib/math-base-assert-is-nan';
import sqrt from '@stdlib/math-base-special-sqrt';
import PI from '@stdlib/constants-float64-pi';


// VARIABLES //

var SQRT_TWO_OVER_PI = sqrt( 2.0 / PI );


// MAIN //

/**
* Returns the expected value for a half-normal distribution with scale parameter `sigma`.
*
* @param {PositiveNumber} sigma - scale parameter
* @returns {number} expected value
*
* @example
* var y = mean( 1.0 );
* // returns ~0.798
*
* @example
* var y = mean( 4.0 );
* // returns ~3.192
*
* @example
* var y = mean( NaN );
* // returns NaN
*
* @example
* var y = mean( 0.0 );
* // returns NaN
*/
function mean( sigma ) {
	if ( isnan( sigma ) || sigma <= 0.0 ) {
		return NaN;
	}
	return sigma * SQRT_TWO_OVER_PI;
}


// EXPORTS //

export default mean;
